




















































































































export default {
	data() {
		return {
			credit_id: '',
			pro_id: '',
			card: [],
			repayList: [],
			repayDetail:[],
			loading: false,
		}
	},
	computed: {

	},
	onLoad(e) {
		if (e.credit_id && e.pro_id) {
			this.credit_id = e.credit_id;
			this.pro_id = e.pro_id;
		} else {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		}
	},
	onShow() {
		this.getRepayDetail();
	},
	onBackPress() {},
	methods: {
		delRepay(){
			uni.showModal({
				content: "是否确定取消计划？",
				confirmText: "确定",
				cancelText: "取消",
				success:(e)=>{
					if(e.confirm){
						const openid = this.$store.state.openid;
						const sessionKey = this.$store.state.sessionKey;
						const credit_id = this.credit_id;
						const pro_id = this.pro_id;
						const that = this;
						try {
							if(this.loading){
								return;
							}
							this.loading = true;
							this.$http.post(this.websiteUrl + '/api/repay/delRepay', {
								openid,
								sessionKey,
								credit_id,
								pro_id
							}, (res) => {
								if (res.data.code == 1) {
									uni.showModal({
										content:res.data.msg,
										showCancel:false,
										success:function() {
											that.loading = false;
											that.getRepayDetail();
										}
									})
								}else{
									uni.showModal({
										content:res.data.msg,
										showCancel:false,
										success:function() {
											that.loading = false;
										}
									})
								}
							});
						} catch (e) {
							uni.showModal({
								content: '网络异常,请稍后重试...',
								showCancel: false,
								success:function() {
									that.loading = false;
								}
							})
						}
					}
				}
			});
		},
		getRepayDetail() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_id;
			const pro_id = this.pro_id;
			try {
				this.$http.post(this.websiteUrl + '/api/repay/getRepayDetail', {
					openid,
					sessionKey,
					credit_id,
					pro_id
				}, (res) => {
					if (res.data.code == 1) {
						this.card = res.data.result.creditCard;
						this.repayDetail = res.data.result.repayDetail;
						this.repayList = res.data.result.repayList;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		}
	}
}
